.text {
  position: relative;
  &__rectangle {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: var(--secondary);
    transition-delay: var(--basic-transition);
    transition: var(--medium-transition);
    animation: slidedown 1s forwards;
  }
}

.slidedown {
  transform: translateY(100%);
  opacity: 0;
}

@keyframes slidedown {
  0% {
    transform: translateY(0);
    opacity: 1;
  }
  60% {
    transform: translateY(100%);
    opacity: 1;
  }
  100% {
    transform: translateY(100%);
    opacity: 0;
  }
}
